'use client';

import React from 'react';
import { Button } from 'antd';
import { 
  UndoOutlined, 
  CloseOutlined, 
  HeartFilled, 
  StarFilled, 
  ThunderboltFilled 
} from '@ant-design/icons';

interface ActionButtonsProps {
  onUndo: () => void;
  onPass: () => void;
  onSuperLike: () => void;
  onLike: () => void;
  onBoost: () => void;
  canUndo: boolean;
  hasCard: boolean;
}

export function ActionButtons({
  onUndo,
  onPass,
  onSuperLike,
  onLike,
  onBoost,
  canUndo,
  hasCard
}: ActionButtonsProps) {
  if (!hasCard) return null;

  return (
    <div className="absolute bottom-0 left-0 right-0 px-4 pb-6 bg-gradient-to-t from-white via-white to-transparent pt-8 z-40">
      <div className="flex items-center justify-center gap-3">
        {/* Undo Button */}
        <Button
          className="w-12 h-12 rounded-full bg-white border border-gray-200 flex items-center justify-center shadow-lg hover:scale-110 active:scale-95 transition-transform disabled:opacity-50 disabled:cursor-not-allowed"
          onClick={onUndo}
          disabled={!canUndo}
          icon={<UndoOutlined className="text-gray-600 text-lg" />}
        />

        {/* Pass Button */}
        <Button
          className="w-16 h-16 rounded-full bg-white border border-gray-200 flex items-center justify-center shadow-lg hover:scale-110 active:scale-95 transition-transform hover:border-red-300"
          onClick={onPass}
          icon={<CloseOutlined className="text-red-500 text-2xl" />}
        />

        {/* Super Like Button */}
        <Button
          className="w-12 h-12 rounded-full bg-white border border-gray-200 flex items-center justify-center shadow-lg hover:scale-110 active:scale-95 transition-transform hover:border-blue-300"
          onClick={onSuperLike}
          icon={<StarFilled className="text-blue-500 text-lg" />}
        />

        {/* Like Button */}
        <Button
          className="w-16 h-16 rounded-full bg-gradient-to-r from-pink-500 to-red-500 border-0 flex items-center justify-center shadow-lg hover:scale-110 active:scale-95 transition-transform"
          onClick={onLike}
          icon={<HeartFilled className="text-white text-2xl" />}
        />

        {/* Boost Button */}
        <Button
          className="w-12 h-12 rounded-full bg-white border border-gray-200 flex items-center justify-center shadow-lg hover:scale-110 active:scale-95 transition-transform hover:border-purple-300"
          onClick={onBoost}
          icon={<ThunderboltFilled className="text-purple-500 text-lg" />}
        />
      </div>
      
      {/* Button Labels */}
      <div className="flex items-center justify-center gap-3 mt-2">
        <div className="w-12 text-center">
          <span className="text-xs text-gray-500">Undo</span>
        </div>
        <div className="w-16 text-center">
          <span className="text-xs text-gray-500">Pass</span>
        </div>
        <div className="w-12 text-center">
          <span className="text-xs text-gray-500">Super</span>
        </div>
        <div className="w-16 text-center">
          <span className="text-xs text-gray-500">Like</span>
        </div>
        <div className="w-12 text-center">
          <span className="text-xs text-gray-500">Boost</span>
        </div>
      </div>
    </div>
  );
}