'use client';

import React, { useState, useRef, useEffect } from 'react';
import { Badge, Tag } from 'antd';
import { EnvironmentOutlined, HeartFilled, CloseOutlined, StarFilled, CheckCircleFilled } from '@ant-design/icons';
import { Profile, AdCard, isProfile } from '../../data/swipeData';

interface SwipeCardProps {
  card: Profile | AdCard;
  onSwipe: (direction: "left" | "right") => void;
  style?: React.CSSProperties;
  triggerSwipe?: "left" | "right" | null;
  onSwipeComplete?: () => void;
  isTopCard?: boolean;
}

export function SwipeCard({ 
  card, 
  onSwipe, 
  style = {}, 
  triggerSwipe, 
  onSwipeComplete,
  isTopCard = false 
}: SwipeCardProps) {
  const [isDragging, setIsDragging] = useState(false);
  const [dragOffset, setDragOffset] = useState({ x: 0, y: 0 });
  const [rotation, setRotation] = useState(0);
  const cardRef = useRef<HTMLDivElement>(null);
  const startPos = useRef({ x: 0, y: 0 });

  // Handle programmatic swipe triggers
  useEffect(() => {
    if (triggerSwipe && isTopCard) {
      const direction = triggerSwipe === 'right' ? 1 : -1;
      animateSwipeOut(direction);
    }
  }, [triggerSwipe, isTopCard]);

  const animateSwipeOut = (direction: number) => {
    if (!cardRef.current) return;

    const targetX = direction * 400;
    const targetRotation = direction * 30;

    cardRef.current.style.transition = 'transform 0.4s ease-out, opacity 0.4s ease-out';
    cardRef.current.style.transform = `translateX(${targetX}px) rotate(${targetRotation}deg)`;
    cardRef.current.style.opacity = '0';

    setTimeout(() => {
      onSwipe(direction > 0 ? 'right' : 'left');
      onSwipeComplete?.();
    }, 400);
  };

  const handleMouseDown = (e: React.MouseEvent) => {
    if (!isTopCard) return;
    
    e.preventDefault();
    setIsDragging(true);
    startPos.current = { x: e.clientX, y: e.clientY };
  };

  const handleTouchStart = (e: React.TouchEvent) => {
    if (!isTopCard) return;
    
    setIsDragging(true);
    const touch = e.touches[0];
    startPos.current = { x: touch.clientX, y: touch.clientY };
  };

  const handleMouseMove = (e: React.MouseEvent) => {
    if (!isDragging || !isTopCard) return;

    const deltaX = e.clientX - startPos.current.x;
    const deltaY = e.clientY - startPos.current.y;
    
    setDragOffset({ x: deltaX, y: deltaY });
    setRotation(deltaX * 0.1); // Subtle rotation based on drag
  };

  const handleTouchMove = (e: React.TouchEvent) => {
    if (!isDragging || !isTopCard) return;

    const touch = e.touches[0];
    const deltaX = touch.clientX - startPos.current.x;
    const deltaY = touch.clientY - startPos.current.y;
    
    setDragOffset({ x: deltaX, y: deltaY });
    setRotation(deltaX * 0.1);
  };

  const handleDragEnd = () => {
    if (!isDragging || !isTopCard) return;

    setIsDragging(false);

    const threshold = 120;
    if (Math.abs(dragOffset.x) > threshold) {
      const direction = dragOffset.x > 0 ? 1 : -1;
      animateSwipeOut(direction);
    } else {
      // Snap back to center
      if (cardRef.current) {
        cardRef.current.style.transition = 'transform 0.3s ease-out';
        cardRef.current.style.transform = 'translateX(0) translateY(0) rotate(0deg)';
      }
      setDragOffset({ x: 0, y: 0 });
      setRotation(0);
    }
  };

  // Add global event listeners for mouse/touch events
  useEffect(() => {
    const handleGlobalMouseMove = (e: MouseEvent) => {
      if (!isDragging || !isTopCard) return;
      
      const deltaX = e.clientX - startPos.current.x;
      const deltaY = e.clientY - startPos.current.y;
      
      setDragOffset({ x: deltaX, y: deltaY });
      setRotation(deltaX * 0.1);
    };

    const handleGlobalTouchMove = (e: TouchEvent) => {
      if (!isDragging || !isTopCard) return;
      
      const touch = e.touches[0];
      const deltaX = touch.clientX - startPos.current.x;
      const deltaY = touch.clientY - startPos.current.y;
      
      setDragOffset({ x: deltaX, y: deltaY });
      setRotation(deltaX * 0.1);
    };

    if (isDragging) {
      document.addEventListener('mousemove', handleGlobalMouseMove);
      document.addEventListener('mouseup', handleDragEnd);
      document.addEventListener('touchmove', handleGlobalTouchMove);
      document.addEventListener('touchend', handleDragEnd);
    }

    return () => {
      document.removeEventListener('mousemove', handleGlobalMouseMove);
      document.removeEventListener('mouseup', handleDragEnd);
      document.removeEventListener('touchmove', handleGlobalTouchMove);
      document.removeEventListener('touchend', handleDragEnd);
    };
  }, [isDragging, isTopCard]);

  const cardTransform = isTopCard 
    ? `translateX(${dragOffset.x}px) translateY(${dragOffset.y}px) rotate(${rotation}deg)`
    : 'translateX(0) translateY(0) rotate(0deg)';

  const likeOpacity = Math.max(0, Math.min(1, dragOffset.x / 150));
  const nopeOpacity = Math.max(0, Math.min(1, Math.abs(dragOffset.x) / 150));

  if (isProfile(card)) {
    return (
      <div
        ref={cardRef}
        className={`absolute w-full h-full bg-white rounded-2xl shadow-xl overflow-hidden ${
          isTopCard ? 'cursor-grab active:cursor-grabbing' : 'pointer-events-none'
        }`}
        style={{
          transform: cardTransform,
          zIndex: isTopCard ? 50 : 40,
          ...style
        }}
        onMouseDown={handleMouseDown}
        onTouchStart={handleTouchStart}
      >
        {/* Profile Image */}
        <div className="relative w-full h-full">
          <img
            src={card.images[0]}
            alt={card.name}
            className="w-full h-full object-cover"
            draggable={false}
          />
          
          {/* Gradient Overlay */}
          <div className="absolute inset-0 bg-gradient-to-b from-transparent via-transparent to-black/70" />

          {/* Like/Nope Indicators */}
          {isTopCard && dragOffset.x > 50 && (
            <div 
              className="absolute top-16 left-8 border-4 border-green-500 text-green-500 px-6 py-3 rounded-xl rotate-[-20deg] shadow-lg bg-white/90"
              style={{ opacity: likeOpacity }}
            >
              <span className="text-3xl font-bold">LIKE</span>
            </div>
          )}

          {isTopCard && dragOffset.x < -50 && (
            <div 
              className="absolute top-16 right-8 border-4 border-red-500 text-red-500 px-6 py-3 rounded-xl rotate-[20deg] shadow-lg bg-white/90"
              style={{ opacity: nopeOpacity }}
            >
              <span className="text-3xl font-bold">NOPE</span>
            </div>
          )}

          {/* Online Status */}
          {card.isOnline && (
            <div className="absolute top-4 right-4 w-3 h-3 bg-green-500 rounded-full border-2 border-white"></div>
          )}

          {/* Verification Badge */}
          {card.verified && (
            <div className="absolute top-4 left-4">
              <CheckCircleFilled className="text-blue-500 text-xl" />
            </div>
          )}

          {/* Profile Info */}
          <div className="absolute bottom-0 left-0 right-0 p-6 text-white">
            <div className="flex items-end justify-between mb-3">
              <div className="flex-1">
                <h2 className="text-3xl font-bold mb-1">
                  {card.name}, {card.age}
                </h2>
                
                <div className="flex items-center gap-1 text-white/90 mb-2">
                  <EnvironmentOutlined className="text-sm" />
                  <span className="text-sm">{card.location || 'San Francisco, CA'}</span>
                </div>
                
                <div className="flex items-center gap-1 text-white/80 mb-3">
                  <span className="text-sm">{card.distance} km away</span>
                </div>

                <p className="text-sm text-white/90 mb-3 leading-relaxed">
                  {card.bio}
                </p>

                {/* Interests Tags */}
                <div className="flex flex-wrap gap-1">
                  {card.interests.slice(0, 3).map((interest, index) => (
                    <Tag 
                      key={index}
                      className="bg-white/20 text-white border-white/30 text-xs"
                    >
                      {interest}
                    </Tag>
                  ))}
                  {card.interests.length > 3 && (
                    <Tag className="bg-white/20 text-white border-white/30 text-xs">
                      +{card.interests.length - 3}
                    </Tag>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  // Ad Card Rendering
  return (
    <div
      ref={cardRef}
      className={`absolute w-full h-full bg-white rounded-2xl shadow-xl overflow-hidden ${
        isTopCard ? 'cursor-grab active:cursor-grabbing' : 'pointer-events-none'
      }`}
      style={{
        transform: cardTransform,
        zIndex: isTopCard ? 50 : 40,
        ...style
      }}
      onMouseDown={handleMouseDown}
      onTouchStart={handleTouchStart}
    >
      {/* Ad Content */}
      <div className="relative w-full h-full">
        <img
          src={card.image}
          alt={card.title}
          className="w-full h-full object-cover"
          draggable={false}
        />
        
        <div className="absolute inset-0 bg-gradient-to-b from-transparent via-transparent to-black/80" />

        {/* Ad Label */}
        <div className="absolute top-4 left-4 bg-yellow-500 text-black px-2 py-1 rounded text-xs font-bold">
          SPONSORED
        </div>

        {/* Ad Content */}
        <div className="absolute bottom-0 left-0 right-0 p-6 text-white">
          <h2 className="text-2xl font-bold mb-2">{card.title}</h2>
          <p className="text-white/90 mb-4">{card.description}</p>
          
          <div className="bg-white text-black px-6 py-3 rounded-full text-center font-bold">
            {card.callToAction}
          </div>
        </div>
      </div>
    </div>
  );
}