'use client';

import React from 'react';
import { SwipeCard } from './SwipeCard';
import { SwipeCardItem } from '../../data/swipeData';

interface SwipeCardStackProps {
  cards: SwipeCardItem[];
  onSwipe: (direction: "left" | "right") => void;
  triggerSwipe?: "left" | "right" | null;
  onSwipeComplete?: () => void;
}

export function SwipeCardStack({ 
  cards, 
  onSwipe, 
  triggerSwipe, 
  onSwipeComplete 
}: SwipeCardStackProps) {
  if (cards.length === 0) return null;

  return (
    <div className="relative h-full px-4 pt-4 pb-20">
      {/* Show up to 3 cards in the stack for performance */}
      {cards.slice(-3).reverse().map((card, index) => {
        const isTopCard = index === 0;
        
        // Card stacking effect - each card is slightly smaller and offset
        const cardStyle: React.CSSProperties = {
          zIndex: 50 - index,
          transform: `scale(${1 - index * 0.02}) translateY(${index * -8}px)`,
          opacity: index === 0 ? 1 : 0.8 - index * 0.2
        };

        return (
          <SwipeCard
            key={`${card.id}-${cards.length}`} // Include length to force re-render
            card={card}
            onSwipe={onSwipe}
            style={cardStyle}
            triggerSwipe={isTopCard ? triggerSwipe : null}
            onSwipeComplete={onSwipeComplete}
            isTopCard={isTopCard}
          />
        );
      })}
    </div>
  );
}